import React, { useState, useEffect } from "react";
import "./jokesStyles.css";

export default function Jokes() {
  const [page, setPage] = useState(1);
  const [jokeList, setJokeList] = useState([]);
  const [isError, setIsError] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [hasMoreJokes, setHasMoreJokes] = useState(false);

  const loadMoreJokes = () => {
    setPage(page + 1);
  };

  useEffect(() => {
    setIsLoading(true);
    fetch(`https://icanhazdadjoke.com/search?page=${page}`, {
      method: "GET",
      headers: new Headers({
        Accept: "application/json"
      })
    })
      .then((res) => res.json())
      .then((response) => {
        setIsLoading(false);
        if (response && response.results) {
          if (response.results.length > 0) {
            setJokeList([...jokeList, ...response.results]);
            setHasMoreJokes(true);
          } else {
            setHasMoreJokes(false);
          }
        }
      })
      .catch((error) => {
        setIsLoading(false);
        setIsError(true);
        console.log(error);
      });
  }, [page]);

  return (
    <div className="jokes_page">
      <h2 className="page_heading">Jokes</h2>

      {isError && <p>There is an Error!!! Please load the page again.</p>}

      <ul>
        {jokeList &&
          jokeList.map((element) => (
            <li key={element.id}>{element.joke}</li>
          ))}
      </ul>

      {isLoading && <p>Wait I'm Loading Jokes for you</p>}

      {hasMoreJokes ? (
        <button
          disabled={isLoading}
          className="load_more"
          onClick={loadMoreJokes}
        >
          Load More Jokes
        </button>
      ) : isLoading || isError ? (
        ""
      ) : (
        <p className="no_more_data"> No More Jokes there !!!</p>
      )}
    </div>
  );
}
